
span.disabled {
  opacity: 0.5;
}
.list > span:after {
  content: ", ";
}
.list > span:last-child:after {
  content: "";
}
tr > th {
  text-align: center;
}
.row {
  padding-left: 30px;
  padding-right: 30px;
}
#boosts {
  text-align: left;
}
#methods {
  width: 1026px;
  margin: auto;
}
#methods.table > thead > tr > th, #methods.table > tbody > tr > td {
  white-space: normal;
}



